<template>
  <div class="container">
    <h1>快速命令</h1>
    <div class="input-output-container">
      <div class="input-area">
        <div class="option-list-container">
          <div class="device-type-option" v-for="item in deviceList" :key="item.id">
            <input type="radio" :value="item.id" v-model="deviceId">
            {{ item.name }}
          </div>
          <div style="margin-left: 20px;background-color: gray;">命令类型:</div>
          <select class="cmd-type-option" v-model="cmdType">
            <option value="">all</option>
            <option value="adb">adb</option>
            <option value="hdc">hdc</option>
            <option value="cmd">cmd</option>
          </select>

          <select class="cmd-type-option" v-model="isShowName">
            <option :value="true">名称</option>
            <option :value="false">命令</option>
          </select>

          <select class="cmd-type-option" v-model="isOnNewWindow">
            <option value="true">新窗口</option>
            <option value="false">浏览器</option>
          </select>
        </div>

        <div class="input-param">
          <div>输入命令：</div>
          <input style="flex-grow: 1" type="text" v-model="rawCmdString" @keyup.enter="executeCmd(-1, rawCmdString)">
        </div>
        <div class="cmd-list-area">
          <div class="name-button" v-for="item in cmdList" :key="item.id">
            <button v-on:click="executeCmd(item.id)" @contextmenu.prevent="updateParam(item.id)" >{{isShowName ? item.name : item.cmd}}</button>
          </div>
        </div>
      </div>

      <div class="out-area">
        <div class="param-area">
          <div class="param-list" v-for="(value, key) in cmdParams" :key="key">
            <label class="param-label">{{ key }}:</label>
            <input type="text" v-model="cmdParams[key]" placeholder="placeholder" name="name">
          </div>
        </div>
        <div class="">{{ output.code }}</div>
        <div class="cmd-area">{{ output.cmd }}</div>
        <div class="output-area">{{ output.msg }}</div>
      </div>

    </div>
  </div>
</template>

<script>
import Axios from 'axios';

export default {
  props: {
    msg: String
  },
  data() {
    return {
      output: {
        code: 1,
        msg: '输出的信息',
        cmd: 'cmd命令信息',
      },
      rawCmdString: "",
      cmdParams: {},
      isOnNewWindow: true,
      deviceId: 1,
      cmdType: '',
      isShowName: true,
      deviceList: [{
        id: '1',
        name: '手机'
      }, {
        id: '2',
        name: '海外手机'
      }, {
        id: '3',
        name: '车机'
      }, {
        id: '4',
        name: '手表'
      }],
      cmdList: []
    }
  },
  watch: {
    cmdType: function (newVal) {
      console.log("new val: " + newVal + ",old value: ");
      Axios
          .get('http://localhost:8073/query?type=' + newVal)
          .then(response => {
            console.log(JSON.stringify(response.data.data.length));
            this.cmdList.length = 0;
            response.data.data.forEach(e => {
              console.log(JSON.stringify(e.name));
              this.cmdList.splice(-1, 0, e)
            })
          })
          .catch(error => console.log(error))
    }
  },
  created() {
    console.log("openAdb")
    Axios
        .get('http://localhost:8073/query')
        .then(response => {
          console.log(JSON.stringify(response.data.data.length))
          response.data.data.forEach( e => {
            this.cmdList.splice(-1, 0, e)
          })
        })
        .catch(error => console.log(error))
  },
  methods: {
    executeCmd: function (id, rawCmd) {
      console.log("id is" + id + ",raw cmd is:" + rawCmd);
      let param = {};
      param.cmdId = id;
      if (this.cmdParams.cmdId === id) {
        param.paramMap = this.cmdParams;
      }
      param.rawCmdString = rawCmd;
      param.onNewWindow = this.isOnNewWindow;
      this.output.code = 88888;
      Axios
          .post('http://localhost:8073/execute', param)
          .then(response => {
            console.log(response.data);
            if (response.data.code === 0) {
              this.output.code = response.data.code;
              this.output.cmd = response.data.data.cmdStr;
              this.output.msg = response.data.data.outputStr;
            } else {
              this.output.code = response.data.code;
              this.output.msg = response.data.msg;
            }

          })
          .catch(error => console.log(error))
    },
    updateParam: function (cmdId) {
      console.log("update param, cmd id:" + cmdId);
      Axios
          .get('http://localhost:8073/queryParams?cmdId=' + cmdId + "&deviceId=" + this.deviceId)
          .then(response => {
            console.log(JSON.stringify(response.data.data))
            if (response.data.code === 0) {
              this.cmdParams = response.data.data;
            } else {
              alert(response.data.msg)
            }
          })
          .catch(error => console.log(error))
    },
    testMethod() {
      alert("执行方法成功")
    }
  },
  mounted() {
    console.log("应用启动啦")
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container {
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.input-output-container {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.input-area {
  display: flex;
  width: 50%;
  flex-direction: column;
  background-color: #42b983;
  justify-content: flex-start;
  align-items: flex-start;
}

.out-area {
  display: flex;
  flex-direction: column;
  width: 50%;
}

.param-area {
  display: flex;
  flex-direction: column;
  width: 100%;
  background-color: lightblue;
}

.param-list {
  display: flex;
  width: 100%;
}

.param-label {
  text-align: left;
  font-size: 20px;
  margin-right: 5px;
  margin-top: 5px;
}

.cmd-area {
  color: #ffffff;
  white-space: pre-wrap;
  text-align: left;
  margin-bottom: 10px;
  background-color: #000000;
  padding: 20px;
}

.output-area {
  padding-top: 10px;
  height: 500px;
  color: #ffffff;
  white-space: pre-wrap;
  text-align: left;
  background-color: #000000;
  overflow: scroll;
}

.input-param {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.option-list-container {
  display: flex;
  flex-direction: row;
  margin-left: 3px;
  margin-top: 2px;
}

.cmd-list-area {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.cmd-type-option {
  background-color: gray;
  border-style: solid;
  border-width: 2px;
  border-color: black;
}

.device-type-option {
  background-color: yellow;
}

.name-button {
  margin-bottom: 5px;
  margin-right: 2px;
}
</style>
